import PropTypes from 'prop-types';
import { createContext, useState, useEffect } from 'react';

// -------------------------------

const initialState = {
  connectedStatus: false,
  walletAddress: '',
  walletType: '',
  JUICEBalance: 0
};

const WalletContext = createContext(initialState);

function WalletProvider({ children }) {
  const [wallet, setWallet] = useState(initialState);

  const onChangeConnect = (status, selectedWalletType, address, JUICEBal) => {
    console.info(status, selectedWalletType, address, JUICEBal);

    // Connect
    if (status === 'connect') {
      console.info('Connect to a Wallet');
      setWallet({
        connectedStatus: true,
        walletAddress: address,
        walletType: selectedWalletType,
        JUICEBalance: JUICEBal
      });
    }

    // Disconnect
    if (status === 'disconnect') {
      console.info('Disconnect a Wallet');
      setWallet({
        connectedStatus: false,
        walletAddress: '',
        walletType: '',
        JUICEBalance: 0
      });
    }
  };

  return (
    <WalletContext.Provider
      value={{
        ...wallet,
        onChangeConnect
      }}
    >
      {children}
    </WalletContext.Provider>
  );
}

export { WalletProvider, WalletContext };
