import { circularProgressClasses } from '@material-ui/core';
import { replace } from 'lodash';
import numeral from 'numeral';

// ----------------------------------------------------------------------

export function fCurrency(number) {
  return numeral(number).format(Number.isInteger(number) ? '$0,0' : '$0,0.00');
}

export function fMCurrency(number) {
  return numeral(number).format(Number.isInteger(number) ? '0,0' : '0,0.00');
}

export function fCRCurrency(numberInp) {
  const number = parseFloat(numberInp)
  // console.log("NUMBER", number)

  if (number === 0 || number === '0') return '0'

  if (!number) return '-'

  let commaFlag = "0,0.000000"
  if (parseInt(number, 10) < 1) {
    commaFlag = "0,0.0000000000"
  } else if (parseInt(number, 10) < 100) {
    commaFlag = "0,0.0000000"
  } else if (parseInt(number, 10) < 1000) {
    commaFlag = "0,0.0000"
  } else if (parseInt(number, 10) < 100000) {
    commaFlag = "0,0.00"
  } else {
    commaFlag = "0,0.00"
  }

  return numeral(number).format(Number.isInteger(number) ? '0,0' : commaFlag)
}

export function fPercent(number) {
  return numeral(number / 100).format('0.0%');
}

export function fNumber(number) {
  return numeral(number).format();
}

export function fShortenNumber(number) {
  return replace(numeral(number).format('0.00a'), '.00', '');
}

export function fData(number) {
  return numeral(number).format('0.0 b');
}

export function formatNumber(value) {
  if(!value || value === 0) return 0

  const getPrecision = getPrecisionValue();
  const formatted = Number(value).toFixed(getPrecision);

  return formatted;

  function getPrecisionValue() {
    let precisionValue = 2;

    if (value < 0.0000000000000000001) {
      precisionValue = 20;
    } else if (value < 0.000000000000000001) {
      precisionValue = 19;
    } else if (value < 0.00000000000000001) {
      precisionValue = 18;
    } else if (value < 0.0000000000000001) {
      precisionValue = 17;
    } else if (value < 0.000000000000001) {
      precisionValue = 16;
    } else if (value < 0.00000000000001) {
      precisionValue = 15;
    } else if (value < 0.000000000001) {
      precisionValue = 14;
    } else if (value < 0.00000000001) {
      precisionValue = 13;
    } else if (value < 0.0000000001) {
      precisionValue = 12;
    } else if (value < 0.000000001) {
      precisionValue = 11;
    } else if (value < 0.00000001) {
      precisionValue = 10;
    } else if (value < 0.0000001) {
      precisionValue = 9;
    } else if (value < 0.000001) {
      precisionValue = 8;
    } else if (value < 0.00001) {
      precisionValue = 7;
    } else if (value < 0.0001) {
      precisionValue = 6;
    } else if (value < 0.001) {
      precisionValue = 5;
    } else if (value < 0.01) {
      precisionValue = 4;
    } else if (value < 0.1) {
      precisionValue = 3;
    } else if (value < 1000) {
      precisionValue = 2;
    } else if (value < 10000) {
      precisionValue = 2;
    } else if (value < 100000) {
      precisionValue = 1;
    } else {
      precisionValue = 0;
    }
    return precisionValue;
  }
}
