import { Suspense, lazy } from 'react';
import { Navigate, useRoutes, useLocation } from 'react-router-dom';
// layouts
import MainLayout from '../layouts/main';
import DashboardLayout from '../layouts/dashboard';
import LogoOnlyLayout from '../layouts/LogoOnlyLayout';
// components
import LoadingScreen from '../components/LoadingScreen';

// ----------------------------------------------------------------------

const Loadable = (Component) => (props) => {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const { pathname } = useLocation();
  const isDashboard = pathname.includes('/dashboard');

  return (
    <Suspense
      fallback={
        <LoadingScreen
          sx={{
            ...(!isDashboard && {
              top: 0,
              left: 0,
              width: 1,
              zIndex: 9999,
              position: 'fixed'
            })
          }}
        />
      }
    >
      <Component {...props} />
    </Suspense>
  );
};

export default function Router() {
  return useRoutes([
    // Main Routes
    {
      path: '*',
      element: <LogoOnlyLayout />,
      children: [
        { path: '404', element: <NotFound /> },
        { path: '*', element: <Navigate to="/404" replace /> },
        { path: 'beta', element: <BetaPage /> }
      ]
    },

    // Live
    {
      path: '/',
      element: <DashboardLayout />,
      children: [
        { path: '/', element: <Navigate to="/welcome" replace /> },

        { path: 'welcome', element: <WelcomePage /> },
        { path: 'mybets', element: <MyBetsPage /> },
        { path: 'stake', element: <StakePage /> },
        { path: 'mining', element: <MiningPage /> },
        { path: 'claimjuice', element: <ClaimJuicePage /> },
        { path: 'leaderboarddrop', element: <LeaderboardDropPage /> },
        { path: 'thecommittee', element: <TheCommitteePage /> },
        { path: 'play', element: <PlayPage /> },
        { path: 'lucky888', element: <Lucky888Page /> },
        { path: 'twowin', element: <TwoWinPage /> },
        { path: 'shell', element: <ShellPage /> },
        { path: 'envelopes', element: <EnvelopesPage /> },
        { path: 'apeslots', element: <ApeSlotsPage /> },
        { path: 'hashlottery', element: <HashLotteryPage /> },
        { path: 'plinko', element: <PlinkoPage /> }
      ]
    },

    { path: '*', element: <Navigate to="/404" replace /> }
  ]);
}

// IMPORT COMPONENTS

// GENERAL
const WelcomePage = Loadable(lazy(() => import('../pages/WelcomePage')));

const StakePage = Loadable(lazy(() => import('../pages/StakePage')));

const MyBetsPage = Loadable(lazy(() => import('../pages/MyBetsPage')));

const LeaderboardDropPage = Loadable(lazy(() => import('../pages/LeaderboardDropPage')));

const TheCommitteePage = Loadable(lazy(() => import('../pages/TheCommitteePage')));

const HashLotteryPage = Loadable(lazy(() => import('../pages/HashLotteryPage')));

const ClaimJuicePage = Loadable(lazy(() => import('../pages/ClaimJuicePage')));

const MiningPage = Loadable(lazy(() => import('../pages/MiningPage')));

// GAMES
const PlayPage = Loadable(lazy(() => import('../pages/PlayPage')));

const Lucky888Page = Loadable(lazy(() => import('../pages/Lucky888Page')));

const TwoWinPage = Loadable(lazy(() => import('../pages/TwoWinPage')));

const ShellPage = Loadable(lazy(() => import('../pages/ShellPage')));

const EnvelopesPage = Loadable(lazy(() => import('../pages/EnvelopesPage')));

const ApeSlotsPage = Loadable(lazy(() => import('../pages/ApeSlotsPage')));

const PlinkoPage = Loadable(lazy(() => import('../pages/PlinkoPage')));

const NotFound = Loadable(lazy(() => import('../pages/Page404')));

const Maintenance = Loadable(lazy(() => import('../pages/Maintenance')));

// BETA
const BetaPage = Loadable(lazy(() => import('../pages/BetaPage')));
