import Lottie from 'react-lottie';
import { AnimatePresence, motion } from 'framer-motion';
import { varFadeInRight, varFadeOutLeft, varFadeOutRight } from '../animate';

import MiningLottie from '../../assets/mining/mining.json';
import { Stack } from '@material-ui/core';

const miningOptions = {
  loop: true,
  autoplay: true,
  animationData: MiningLottie,
  background: 'transparent'
};

const dropIn = {
  hidden: {
    x: '5vw',
    opacity: 0
  },
  visible: {
    x: '0',
    opacity: 1,
    transition: {
      duration: 1,
      type: 'spring',
      damping: 15,
      stiffnesss: 300
    }
  },
  exit: {
    x: '10vw',
    opacity: 0
  }
};

export default function MiningSnackbar(props) {
  return (
    <AnimatePresence initial={false}>
      {props.visible && (
        <motion.div variants={dropIn} initial="hidden" animate="visible" exit="exit" className="mining-snackbar">
          <Stack justifyContent="center">
            <div className="mining-snackbar-lottie">
              <Lottie options={miningOptions} height="100%" width="100%" isClickToPauseDisabled />
            </div>
            <div className="mining-snackbar-message">
              Mined: <span className="bold-yellow">{props.amount} JUICE</span>
            </div>
          </Stack>
        </motion.div>
      )}
    </AnimatePresence>
  );
}
