import PropTypes from 'prop-types';
// material
import { useTheme } from '@material-ui/core/styles';
import { Box } from '@material-ui/core';
import cryptodropLogo from '../assets/cryptodrop/cryptodropLogo.svg';
import cryptodropLogoHorizontal from '../assets/cryptodrop/cryptodropLogoHorizontal.svg';

// ----------------------------------------------------------------------

Logo.propTypes = {
  sx: PropTypes.object,
  type: PropTypes.string
};

export default function Logo({ sx, type }) {
  const theme = useTheme();
  const PRIMARY_LIGHT = theme.palette.primary.light;
  const PRIMARY_MAIN = theme.palette.primary.main;
  const PRIMARY_DARK = theme.palette.primary.dark;

  if (type === 'horizontal') {
    return (
      <Box sx={{ width: 230, height: 50, ...sx }}>
        <img src={cryptodropLogoHorizontal} alt="Cryptodrop" />
      </Box>
    );
  }
  return (
    <Box sx={{ width: 150, height: 100, ...sx }}>
      <img src={cryptodropLogo} alt="Cryptodrop" />
    </Box>
  );
}
