// routes
import { BSC, MetamaskConnector, DAppProvider} from '@usedapp/core'
import { WalletConnectV2Connector } from '@usedapp/wallet-connect-v2-connector'
import { ApolloClient, ApolloProvider, InMemoryCache } from '@apollo/client';

import Router from './routes';
// theme
import ThemeConfig from './theme';
// components
import Settings from './components/settings';
import RtlLayout from './components/RtlLayout';
import ScrollToTop from './components/ScrollToTop';
import ThemePrimaryColor from './components/ThemePrimaryColor';
import NotistackProvider from './components/NotistackProvider';

import { BetaProvider } from './contexts/BetaContext';


const config = {
  readOnlyChainId: BSC.chainId,
  readOnlyUrls: {
    [BSC.chainId]:process.env.REACT_APP_RPC_URL,
  },
  connectors: {
    metamask: new MetamaskConnector(),
    walletConnectV2: new WalletConnectV2Connector({
      projectId: 'f276742e18c0fc2cae075e4e0154d5c5',
      chains: [BSC],
      rpcMap: {
        56: process.env.REACT_APP_RPC_URL,
      },
    }),
  },
}

const client = new ApolloClient({
  uri: process.env.REACT_APP_GRAPHQL_URL,
  cache: new InMemoryCache()
});

// ----------------------------------------------------------------------

export default function App() {
  return (
    <ApolloProvider client={client}>
      <DAppProvider config={config}>
        <ThemeConfig>
          <ThemePrimaryColor>
            <RtlLayout>

              <BetaProvider>

                <NotistackProvider>
                  <Settings />
                  <ScrollToTop />
                  <Router />
                </NotistackProvider>

              </BetaProvider>

            </RtlLayout>
          </ThemePrimaryColor>
        </ThemeConfig>
      </DAppProvider>
    </ApolloProvider>
  );
}
