import {
  Button,
  List,
  Avatar,
  Dialog,
  DialogTitle,
  DialogActions,
  DialogContent,
  DialogContentText,
  ListItemText,
  ListItemAvatar,
  ListItemButton
} from '@material-ui/core';
import { useState, useEffect } from 'react';
import AlarmIcon from '@material-ui/icons/AccountBalanceWallet';

import { useEthers, shortenAddress, useConnector } from '@usedapp/core';


// COMPONENTS
import CopyClipboard from '../../components/CopyClipboard';

import { wallets, connectorsByName } from '../../connectors';

export default function ConnectWallet(props) {

  const { activateBrowserWallet, account, deactivate, chainId} = useEthers()

  const { error } = useEthers();

  const [activateError, setActivateError] = useState('')

  useEffect(() => {
    if (error && !error.message.includes('revert')) {
      setActivateError(error.message)
    } else {
      setActivateError('')
    }
  }, [error])



  const { connector, isLoading } = useConnector()

  useEffect(() => {
    if (account && !activateError) {

      setSelectedWallet({
        wallet: '',
        address: account,
        status: true,
        color: 'success',
        btnText: shortenAddress(account),
        onClick: handleClickOpenConnected
      });
    } else if (activateError) {

      setSelectedWallet({
        wallet: '',
        address: '',
        status: false,
        color: 'error',
        btnText: activateError,
        onClick: handleClickOpen
      });
    } else {
      setSelectedWallet({
        wallet: '',
        address: '',
        status: false,
        color: 'primary',
        btnText: 'Connect Wallet',
        onClick: handleClickOpen
      });
    }
  }, [account, error]);


  const [open, setOpen] = useState(false);
  const handleClickOpen = () => {
    setActivateError('');
    setOpen(true);
  };

  const [selectedWallet, setSelectedWallet] = useState({
    status: false,
    address: '',
    color: 'primary',
    btnText: 'Connect Wallet',
    onClick: handleClickOpen
  });

  const handleClose = (value) => {
    setOpen(false);
  };

  const handleConnectWallet = (value) => {
    activateBrowserWallet({type:connectorsByName[value]});
    setActivateError('');
    setOpen(false);
  };

  const [openConnected, setOpenConnected] = useState(false);

  const handleClickOpenConnected = () => {
    setOpenConnected(true);
  };

  const handleCloseConnected = () => {
    setOpenConnected(false);
  };

  const handleDisconnect = () => {
    setActivateError('');
    deactivate();
    setOpenConnected(false);
  };

  return (
    <>
      <Button
        size={props.size}
        variant="contained"
        color={selectedWallet.color}
        onClick={selectedWallet.onClick}
        value="connect"
        startIcon={<AlarmIcon />}
      >
        {selectedWallet.btnText}
      </Button>

      <Dialog open={open} onClose={() => handleClose()} fullWidth maxWidth="md">
        <DialogTitle id="simple-dialog-title">Connect a Wallet</DialogTitle>
        <List>
          {wallets.map((wallets) => (
            <ListItemButton onClick={() => handleConnectWallet(wallets)} key={wallets}>
              <ListItemAvatar>
                <Avatar key={wallets} alt="Remy Sharp" src={`/static/wallets/${wallets}.png`} />
              </ListItemAvatar>
              <ListItemText primary={wallets} />
            </ListItemButton>
          ))}
        </List>
      </Dialog>

      <Dialog open={openConnected} onClose={handleCloseConnected} fullWidth maxWidth="md">
        <DialogTitle>Your Wallet ({connector?.connector?.name ?? 'None'})</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            <CopyClipboard value={selectedWallet.address} style={{ marginTop: '1rem' }} />
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseConnected}>Close</Button>
          <Button size="large" variant="outlined" color="error" onClick={handleDisconnect} autoFocus>
            Logout
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
