/* eslint-disable */

import { useState } from 'react';
import PropTypes from 'prop-types';
import { Icon } from '@iconify/react';
import { NavLink as RouterLink, matchPath, useLocation } from 'react-router-dom';
import arrowIosForwardFill from '@iconify/icons-eva/arrow-ios-forward-fill';
import arrowIosDownwardFill from '@iconify/icons-eva/arrow-ios-downward-fill';
// material
import { alpha, useTheme, styled } from '@material-ui/core/styles';
import {
  Box,
  Button,
  List,
  Collapse,
  ListItemText,
  ListItemIcon,
  ListSubheader,
  ListItemButton
} from '@material-ui/core';
import { useEthers } from '@usedapp/core';

import PaperIcon from '@material-ui/icons/Description';

// ----------------------------------------------------------------------

const ListSubheaderStyle = styled((props) => <ListSubheader disableSticky disableGutters {...props} />)(
  ({ theme }) => ({
    ...theme.typography.overline,
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(2),
    paddingLeft: theme.spacing(5),
    color: theme.palette.text.primary
  })
);

const ListItemStyle = styled((props) => <ListItemButton disableGutters {...props} />)(({ theme }) => ({
  ...theme.typography.body2,
  height: '3.4rem',
  position: 'relative',
  textTransform: 'capitalize',
  paddingLeft: theme.spacing(5),
  paddingRight: theme.spacing(2.5),
  fontSize: '1.3rem',
  color: theme.palette.text.secondary,
  '&:before': {
    top: 0,
    right: 0,
    width: 3,
    bottom: 0,
    content: "''",
    display: 'none',
    position: 'absolute',
    borderTopLeftRadius: 4,
    borderBottomLeftRadius: 4,
    backgroundColor: theme.palette.primary.main
  }
}));

const ListItemIconStyle = styled(ListItemIcon)({
  width: 22,
  height: 22,
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center'
});

// ----------------------------------------------------------------------

NavItem.propTypes = {
  active: PropTypes.func,
  isShow: PropTypes.bool,
  item: PropTypes.object,
  vip: PropTypes.bool
};

function NavItem({ item, active, isShow }) {
  const theme = useTheme();
  const isActiveRoot = active(item.path);
  const { title, path, icon, info, children } = item;
  const [open, setOpen] = useState(isActiveRoot);

  const handleOpen = () => {
    setOpen((prev) => !prev);
  };

  const activeRootStyle = {
    color: 'primary.main',
    fontWeight: 'fontWeightMedium',
    bgcolor: alpha(theme.palette.primary.main, theme.palette.action.selectedOpacity),
    '&:before': { display: 'block' }
  };

  const activeVIPRootStyle = {
    color: 'secondary.main',
    fontWeight: 'fontWeightMedium',
    bgcolor: alpha(theme.palette.secondary.main, theme.palette.action.selectedOpacity),
    '&:before': { display: 'block', backgroundColor: 'secondary.main' }
  };

  const activeLuckyRootStyle = {
    color: 'error.main',
    fontWeight: 'fontWeightMedium',
    bgcolor: alpha(theme.palette.error.main, theme.palette.action.selectedOpacity),
    '&:before': { display: 'block', backgroundColor: 'error.main' }
  };

  const activeTwoWinRootStyle = {
    color: 'success.main',
    fontWeight: 'fontWeightMedium',
    bgcolor: alpha(theme.palette.success.main, theme.palette.action.selectedOpacity),
    '&:before': { display: 'block', backgroundColor: 'success.main' }
  };

  const activeShellRootStyle = {
    color: 'pink.main',
    fontWeight: 'fontWeightMedium',
    bgcolor: alpha(theme.palette.pink.main, theme.palette.action.selectedOpacity),
    '&:before': { display: 'block', backgroundColor: 'pink.main' }
  };

  const activeEnvelopesRootStyle = {
    color: 'error.main',
    fontWeight: 'fontWeightMedium',
    bgcolor: alpha(theme.palette.error.main, theme.palette.action.selectedOpacity),
    '&:before': { display: 'block', backgroundColor: 'error.main' }
  };

  const activeApeSlotsRootStyle = {
    color: '#e35f14',
    fontWeight: 'fontWeightMedium',
    bgcolor: alpha(theme.palette.brown.main, theme.palette.action.selectedOpacity),
    '&:before': { display: 'block', backgroundColor: '#a8440c' }
  };

  const activeHashLotteryRootStyle = {
    color: 'carrot.main',
    fontWeight: 'fontWeightMedium',
    bgcolor: alpha(theme.palette.carrot.main, theme.palette.action.selectedOpacity),
    '&:before': { display: 'block', backgroundColor: 'carrot.main' }
  };

  const activePlinkRootStyle = {
    color: 'purple.lighter',
    fontWeight: 'fontWeightMedium',
    bgcolor: alpha(theme.palette.purple.main, theme.palette.action.selectedOpacity),
    '&:before': { display: 'block', backgroundColor: 'purple.main' }
  };

  const activeSubStyle = {
    color: 'text.primary',
    fontWeight: 'fontWeightMedium'
  };

  if (children) {
    return (
      <>
        <ListItemStyle
          onClick={handleOpen}
          sx={{
            ...(isActiveRoot && activeRootStyle)
          }}
        >
          <ListItemIconStyle>{icon && icon}</ListItemIconStyle>

          {isShow && (
            <>
              <ListItemText disableTypography primary={title} />
              {info && info}
              <Box
                component={Icon}
                icon={open ? arrowIosDownwardFill : arrowIosForwardFill}
                sx={{ width: 16, height: 16, ml: 1 }}
              />
            </>
          )}
        </ListItemStyle>

        {isShow && (
          <Collapse in={open} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              {children.map((item) => {
                const { title, path } = item;
                const isActiveSub = active(path);

                return (
                  <ListItemStyle
                    key={title}
                    component={RouterLink}
                    to={path}
                    sx={{
                      ...(isActiveSub && activeSubStyle)
                    }}
                  >
                    <ListItemIconStyle>
                      <Box
                        component="span"
                        sx={{
                          width: 4,
                          height: 4,
                          display: 'flex',
                          borderRadius: '50%',
                          alignItems: 'center',
                          justifyContent: 'center',
                          bgcolor: 'text.disabled',
                          transition: (theme) => theme.transitions.create('transform'),
                          ...(isActiveSub && {
                            transform: 'scale(2)',
                            bgcolor: 'primary.main'
                          })
                        }}
                      />
                    </ListItemIconStyle>
                    <ListItemText disableTypography primary={title} />
                  </ListItemStyle>
                );
              })}
            </List>
          </Collapse>
        )}
      </>
    );
  }

  let listStyle = '';

  if (title === 'Lucky 888') {
    listStyle = isActiveRoot && activeLuckyRootStyle;
  } else if (title === 'Two Win') {
    listStyle = isActiveRoot && activeTwoWinRootStyle;
  } else if (title === 'Shell') {
    listStyle = isActiveRoot && activeShellRootStyle;
  } else if (title === 'Ape Slots') {
    listStyle = isActiveRoot && activeApeSlotsRootStyle;
  } else if (title === 'Envelopes') {
    listStyle = isActiveRoot && activeEnvelopesRootStyle;
  } else if (title === 'Hash Lottery') {
    listStyle = isActiveRoot && activeHashLotteryRootStyle;
  } else if (title === 'Plinko') {
    listStyle = isActiveRoot && activePlinkRootStyle;
  } else {
    listStyle = isActiveRoot && activeRootStyle;
  }

  return (
    <ListItemStyle component={RouterLink} to={path} sx={{ ...listStyle }}>
      <ListItemIconStyle>{icon && icon}</ListItemIconStyle>
      {isShow && (
        <>
          <ListItemText
            disableTypography
            primary={title}
            className={isActiveRoot && title === 'VIP Game' ? 'effect-shine' : ''}
          />
          {info && info}
        </>
      )}
    </ListItemStyle>
  );
}

NavSection.propTypes = {
  isShow: PropTypes.bool,
  navConfig: PropTypes.array
};

export default function NavSection({ navConfig, isShow = true, ...other }) {
  const { account } = useEthers();
  const { pathname } = useLocation();
  const match = (path) => (path ? !!matchPath({ path, end: false }, pathname) : false);

  return (
    <Box {...other}>
      {navConfig.map((list, i) => {
        const { subheader, items } = list;
        return (
          <List key={subheader + i} disablePadding>
            {isShow && <ListSubheaderStyle>{subheader}</ListSubheaderStyle>}
            {items.map((item) =>
              item.id === 'welcome' ? (
                <NavItem key={item.title} item={item} active={match} isShow={isShow} type="reguler" />
              ) : (item.id === 'mybets' || item.id === 'stake') && account ? (
                <NavItem key={item.title} item={item} active={match} isShow={isShow} type="reguler" />
              ) : item.id !== 'mybets' && item.id !== 'stake' ? (
                <NavItem key={item.title} item={item} active={match} isShow={isShow} type="reguler" />
              ) : item.id === 'vipplay' ? (
                <NavItem key={item.title} item={item} active={match} isShow={isShow} type="vip" />
              ) : item.id === 'lucky888' ? (
                <NavItem key={item.title} item={item} active={match} isShow={isShow} type="lucky888" />
              ) : item.id === 'twowin' ? (
                <NavItem key={item.title} item={item} active={match} isShow={isShow} type="twowin" />
              ) : item.id === 'shell' ? (
                <NavItem key={item.title} item={item} active={match} isShow={isShow} type="shell" />
              ) : item.id === 'apeslots' ? (
                <NavItem key={item.title} item={item} active={match} isShow={isShow} type="apeslots" />
              ) : item.id === 'shell' ? (
                <NavItem key={item.title} item={item} active={match} isShow={isShow} type="envelopes" />
              ) : item.id === 'hashlottery' ? (
                <NavItem key={item.title} item={item} active={match} isShow={isShow} type="hashlottery" />
              ) : (
                ''
              )
            )}
          </List>
        );
      })}
      <Button
        variant="contained"
        size="large"
        color="yellow"
        style={{ marginLeft: '2rem', marginTop: '1.6rem', width: '80%' }}
        onClick={(event) =>
          window.open(
            'https://pancakeswap.finance/swap?outputCurrency=0xae5fb0c28fb5f8fc3902708879476a2ce3432015',
            '_blank'
          )
        }
      >
        Buy on Pancake{' '}
        <img src="/static/svg/pancakeswap.svg" alt="" style={{ width: '1.4rem', marginLeft: '0.4rem' }} />
      </Button>

      <Button
        variant="outlined"
        size="large"
        startIcon={<PaperIcon />}
        onClick={(event) => window.open('https://cryptodrop.gg/static/files/Litepaper.pdf', '_blank')}
        style={{ marginLeft: '2rem', marginTop: '0.8rem', width: '80%' }}
      >
        Litepaper
      </Button>
    </Box>
  );
}
